import React from "react";
import "./App.css";
import { Layout, Header, Navigation, Content } from "react-mdl";
import Projects from "./Components/projects";
import LandingPage from "./Components/landingpage";
import AboutMe from "./Components/aboutme";
import Contact from "./Components/contact";
import AOS from "aos";
import "aos/dist/aos.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      bgColor1: "",
      bgColor2: "",
      bgColor3: "",
      offset: 0
    };
  }
  componentDidMount() {
    AOS.init({
      duration: 2000
    });

    window.addEventListener("scroll", this.boxClick1, false);
  }

  componentWillUnmount = () => {
    window.removeEventListener("scroll", this.boxClick0);
    window.removeEventListener("scroll", this.boxClick1);
    window.removeEventListener("scroll", this.boxClick2);
    window.removeEventListener("scroll", this.boxClick3);
  };

  boxClick0 = e => {
    let scr = document.getElementById("name");
    scr.style.color = "grey";
    let element = e.target;
    this.setState({
      bgColor1: "",
      bgColor2: "",
      bgColor3: ""
    });
  };

  boxClick1 = e => {
    let scr = document.getElementById("name");
    scr.style.color = "#06beb6";
    let height = window.scrollY;

    this.setState({
      bgColor1: "#06beb6",
      bgColor2: "",
      bgColor3: ""
    });
  };

  boxClick2 = e => {
    let scr = document.getElementById("name");
    scr.style.color = "white";
    this.setState({
      bgColor1: "",
      bgColor2: "#06beb6",
      bgColor3: ""
    });
  };

  boxClick3 = e => {
    let scr = document.getElementById("name");
    scr.style.color = "aqua";
    this.setState({
      bgColor1: "",
      bgColor2: "",
      bgColor3: "#06beb6"
    });
  };

  render() {
    return (
      <div className="content">
        <Layout>
          <Header className="header-color" scroll data-aos="fade">
            <Navigation data-aos="slide-right">
              <a
                id="name"
                href="#/LP"
                style={{ textAlign: "left" }}
                onClick={this.boxClick0}
              >
                <h3>Anthony Hsia</h3>
              </a>
              <a
                className="about_me"
                href="#AboutMe"
                style={{ backgroundColor: this.state.bgColor1 }}
                onClick={this.boxClick1}
              >
                About Me
              </a>
              <a
                className="projects"
                href="#Projects"
                style={{ backgroundColor: this.state.bgColor2 }}
                onClick={this.boxClick2}
              >
                Projects
              </a>
              <a
                className="contact"
                href="#Contact"
                style={{ backgroundColor: this.state.bgColor3 }}
                onClick={this.boxClick3}
              >
                Contact
              </a>
            </Navigation>
          </Header>
          <Content id="/LP" onScroll={this.boxClick0}>
            <section>
              <LandingPage />
            </section>
            <section id="AboutMe" onScroll={this.boxClick1}>
              <AboutMe />
            </section>

            <section id="Projects" onScroll={this.boxClick2}>
              <Projects />
            </section>
            <section id="Contact" onScroll={this.boxClick3}>
              <Contact />
            </section>
          </Content>
        </Layout>
      </div>
    );
  }
}

export default App;
